import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VisitorReportsComponent } from './visitor-reports/visitor-reports.component';
import { VisitorReportsDataComponent } from './visitor-reports/visitor-reports-data/visitor-reports-data.component';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

const routes: Routes = [
  { path: '', component: VisitorReportsComponent, canActivate: [AuthGuard]  },
	{ path: ':name/:id', component: VisitorReportsDataComponent, canActivate: [AuthGuard]  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VisitorReportRoutingModule { }
